<template lang="">
    <PreloaderVac/>
    <div class="inventory">
        <div class="container">
            <div class="inventory-main">
                <div class="inventory-main_filters" :class="{filterActive: filterDisplay === true}">
                    <img class="filter-close_img" src="../assets/img/inventory/Close2.png" alt="" @click="filterDisplay = !filterDisplay">
                    <div class="filters-title">
                        <h3>Detailed search</h3>
                        <p @click="clearFilters">Clear filters</p>
                    </div>
                    <div class="filters-search">

                        <div class="filter_make-model">
                            <div class="select filter-select">
                                <div class="select__header" @click="selectModel = !selectModel">
                                    <span class="select__current">Make, Model</span>
                                    <div class="select__icon" :class="{activeImg: selectModel === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="" >
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="selectModel === false && optionDonesMake.length">
                                    <div class="option_done-main" v-for="(optionDone, i) in optionDonesMake"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteDoneMake(i)">
                                        <span>{{optionDone}}</span>
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="selectModel === false && optionDonesMadel.length">
                                    <div class="option_done-main" v-for="(optionDone, i) in optionDonesMadel"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteDoneMadel(i)">
                                        <span>{{optionDone}}</span>
                                    </div>
                                </div>

                                <div class="select__body-model" v-if="selectModel === true">
                                    <div class="body-model_main">
                                        <h4 class="model-title">Make</h4>
                                        <input type="text" v-model="searchMake" class="inventory-search filter-select"
                                        placeholder="Search Make...">
                                        <div class="select-option" v-if="searchMake != ''">
                                            <div class="select-option_name" v-for="(make, i) in filterMake" :key="i" 
                                            @click="selectMakeClick(make)" v-if="filterMake.length">
                                                {{make.name}}
                                            </div>
                                            <div class="select-option_name" v-else>
                                                Nothing found
                                            </div>
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionDone, i) in optionDonesMake"
                                            :key="i">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteDoneMake(i)">
                                            <span>{{optionDone}}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="body-model_main">
                                        <h4 class="model-title">Model</h4>
                                        <input type="text" v-model="searchModel" class="inventory-search filter-select"
                                        placeholder="Search Model...">
                                        <div class="select-option" v-if="searchModel != ''">
                                            <div class="select-option_name" v-for="(madel, i) in filterMadel" :key="i"
                                            v-if="filterMadel.length" @click="selectMadelClick(madel)">
                                                {{madel.name}}
                                            </div>
                                            <div class="select-option_name" v-else>
                                                Nothing found
                                            </div>
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionDoneMadel, i) in optionDonesMadel"
                                            :key="i">
                                                <img src="../assets/img/inventory/Close.png" @click="deleteDoneMadel(i)">
                                                <span>{{optionDoneMadel}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select">
                                <div class="select__header" @click="bodyType = !bodyType">
                                    <span class="select__current">Body type</span>
                                    <div class="select__icon" :class="{activeImg: bodyType === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="bodyType === false && optionBodyType.length">
                                    <div class="option_done-main" v-for="(optionType, i) in optionBodyType"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteType(optionType, i)">
                                        <span>{{optionType}}</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="bodyType === true">
                                    <div class="body-model_type">
                                        <div class="select-type" v-for="(type, index) in selectBodyType" :key="index">
                                            <div class="select-option_type" @click="inputCheckboxClick(type, index)">
                                                <input type="checkbox" v-model="type.checked" :id="type.id">
                                                <label :for="type.id">
                                                <img :src="type.img">
                                                {{type.name}}
                                                </label>
                                            </div>
                                            
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionType, i) in optionBodyType"
                                            :key="i">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteType(optionType, i)">
                                            <span>{{optionType}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select">
                                <div class="select__header" @click="transm = !transm">
                                    <span class="select__current">Transmission</span>
                                    <div class="select__icon" :class="{activeImg: transm === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="transm === false && optionTransmission.length">
                                    <div class="option_done-main" v-for="(optionTransm, i) in optionTransmission"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteTransm(optionTransm, i)">
                                        <span>{{optionTransm}}</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="transm === true">
                                    <div class="body-model_type">
                                        <div class="select-type" v-for="(type, index) in selectTransmission" :key="index">
                                            <div class="select-option_type" @click="inputTransmClick(type, index)">
                                                <input type="checkbox" v-model="type.checked" :id="type.id">
                                                <label :for="type.id">
                                                <img :src="type.img">
                                                {{type.name}}
                                                </label>
                                            </div>
                                            
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionTransm, i) in optionTransmission"
                                            :key="i">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteTransm(optionTransm, i)">
                                            <span>{{optionTransm}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select" @click="rangeSlider()">
                                <div class="select__header" @click="price = !price">
                                    <span class="select__current">Price</span>
                                    <div class="select__icon" :class="{activeImg: price === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-price_done" v-if="(minPrice != 0 || maxPrice != 200) && price === false" :class="{margintop: price === false}">
                                    <div class="option_done-main">
                                    <img src="../assets/img/inventory/Close.png" @click="deletePrice()">
                                    <span>{{minPrice}} 000 - {{maxPrice}} 000</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="price === true">
                                    <div class="body-model_price">
                                        <div class="select-price">
                                            <div class="select-price_top">
                                                <p class="minp">$ {{minPrice}} <span class="minspan" :class="{minspanactive: minPrice > 0}">000</span></p>
                                                <p>$ {{maxPrice}} 000</p>
                                            </div>
                                            <div class="select-option_price">
                                                <div class="progress"></div>
                                                <input type="range" class="range-min" min="0" max="200" step="1" v-model.number="minPrice">
                                                <input type="range" class="range-max" min="0" max="200" step="1" v-model.number="maxPrice">
                                            </div>
                                            
                                        </div>
                                        <div class="select-price_done" v-if="minPrice != 0 || maxPrice != 200">
                                            <div class="option_done-main">
                                            <img src="../assets/img/inventory/Close.png" @click="deletePrice()">
                                            <span>{{minPrice}} 000 - {{maxPrice}} 000</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select" @click="rangeSlider2()">
                                <div class="select__header" @click="year = !year">
                                    <span class="select__current">Year</span>
                                    <div class="select__icon" :class="{activeImg: year === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-year_done" v-if="(minYear != 1980 || maxYear != 2023) && year === false" :class="{margintop: year === false}">
                                    <div class="option_done-year">
                                    <img src="../assets/img/inventory/Close.png" @click="deleteYear()">
                                    <span>{{minYear}} - {{maxYear}}</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="year === true">
                                    <div class="body-model_price">
                                        <div class="select-price">
                                            <div class="select-price_top">
                                                <p>{{minYear}}</p>
                                                <p>{{maxYear}}</p>
                                            </div>
                                            <div class="select-option_year">
                                                <div class="progress"></div>
                                                <input type="range" class="range-min" min="1980" max="2023" step="1" v-model.number="minYear">
                                                <input type="range" class="range-max" min="1980" max="2023" step="1" v-model.number="maxYear">
                                            </div>
                                            
                                        </div>
                                        <div class="select-year_done" v-if="minYear != 1980 || maxYear != 2023">
                                            <div class="option_done-year">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteYear()">
                                            <span>{{minYear}} - {{maxYear}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select" @click="rangeSlider3()">
                                <div class="select__header" @click="kilometres = !kilometres">
                                    <span class="select__current">Kilometres</span>
                                    <div class="select__icon" :class="{activeImg: kilometres === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-kilometres_done" v-if="(minKilometres != 200) && kilometres === false" :class="{margintop: year === false}">
                                    <div class="option_done-kilometres">
                                    <img src="../assets/img/inventory/Close.png" @click="deleteKilometres()">
                                    <span>{{minKilometres}} 000 or less</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="kilometres === true">
                                    <div class="body-model_price">
                                        <div class="select-price">
                                            <div class="select-price_top">
                                                <p class="minp">{{minKilometres}}<span class="minspan" :class="{minspanactive: minKilometres > 0}">000</span> or less</p>
                                            </div>
                                            <div class="select-option_kilometres">
                                                <div class="progress"></div>
                                                <input type="range" class="range-min" min="0" max="200" step="1" v-model.number="minKilometres">
                                            </div>
                                            
                                        </div>
                                        <div class="select-kilometres_done" v-if="minKilometres != 200">
                                            <div class="option_done-kilometres">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteKilometres()">
                                            <span>{{minKilometres}} 000 or less</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>







                <div class="inventory-main_search">
                    <div class="filters-btn" @click="filterDisplay = !filterDisplay">
                        <img src="../assets/img/inventory/Filter.png" alt="">
                        <p>Search Filter</p>
                    </div>
                    <div class="search-left">
                        <input type="text" v-model="search" class="inventory-search" placeholder="Find a dream car...">
                        <img src="../assets/img/inventory/Share2.png" alt="">
                    </div>
                    <div class="search-right">
                        <p>Sorted by</p>
                        <div class="select" @click="selectBody = !selectBody">
                            <div class="select__header">
                            <span class="select__current">{{selected}}</span>
                            <div class="select__icon" :class="{activeImg: selectBody === true}">
                                <img src="../assets/img/inventory/Vector3.png" alt="">
                            </div>
                            </div>

                            <div class="select__body" v-if="selectBody === true">
                                <div class="select__item" v-for="(option, i) in options" :key="i" @click="selectOption(option)">
                                    {{option.name}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>






                <div class="inventory-main_cars">
                    <div class="inventory-main_car">
                        <div class="inventory-card" v-for="(inventoryCar, i) in paginatedCars" :key="i"
                        v-if="paginatedCars.length >= 1">
                        <div class="inventory-card_top">
                            <swiper
                                :scrollbar="{
                                hide: false,
                                }"
                                :modules="modules"
                                class="mySwiper6"
                            >
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image2">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image3">
                                </swiper-slide>
                            </swiper>
                        </div>
                        <div class="inventory-card_bottom" @click="cartClick(inventoryCar.id)">
                            <h3>{{inventoryCar.titleCar}} {{inventoryCar.titleModel}}</h3>
                            <p>$ {{inventoryCar.price}} 000</p>
                            <div class="cars-information">
                                <span>{{inventoryCar.year}} year</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.body}}</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.transmission}}</span>
                            </div>
                            <span>{{inventoryCar.kilometres}} 000</span>
                        </div>
                    </div>
                    <div class="inventory-cars_none" v-else="paginatedCars.length < 1">
                        <div class="none-main">
                            <img src="../assets/img/inventory/Sedan.png" alt="">
                            <h3>Unfortunately there are no matches for your query.</h3>
                            <p>Try using other filter settings or request a car of your choice.</p>
                            <router-link to="">Request a car</router-link>
                        </div>
                    </div>
                </div>
                    <div class="btn-toolbar" v-if="paginatedCars.length > 1">
                        <div class="btn-group">
                            <button v-for="p in totalPaginas" @click='pageClick(p)' class="buttonP" 
                            :class="isActive(p)">
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/scrollbar";

// import required modules
import { Scrollbar } from "swiper";
import PreloaderVac from './PreloaderVac.vue'


export default {
    data(){
        return{
            perPage: null,
            // dataPagination: [],
            // paginaActual: 1,
            search: '',
            pageNumber: 1,
            res: [],
            selectBody: false,
            selectModel: false,
            bodyType: false,
            transm: false,
            price: false,
            year: false,
            kilometres: false,
            minPrice: 0,
            maxPrice: 200,
            minYear: 1980,
            maxYear: 2023,
            minKilometres: 200,
            searchMake: '',
            searchModel: '',
            options:[
                {name: 'Recommendations', id: 1},
                {name: 'Newest inventory', id: 2},
                {name: 'Lowest price', id: 3},
                {name: 'Highest price', id: 4},
            ],
            selectMake: [
                {name: 'Porsche', id: 1},
                {name: 'Mitsubishi', id: 2},
                {name: 'Mercedes-Benz', id: 3},
                {name: 'Audi', id: 4},
            ],
            selectMadel: [
                {name: 'Panamera II Turbo S E-Hybrid', id: 1},
                {name: 'Outlander III Restyling', id: 2},
                {name: 'CLA I (C117, X117) 200', id: 3},
                {name: 'W124 420', id: 4},
                {name: 'E-Class III Restyling 230', id: 5},
                {name: 'A7 I (4G) S-tronic', id: 6},
            ],
            selectBodyType: [
                {id: 1, name: 'Trucks', img: require('@/assets/img/inventory/BodyType/Truck.png'), checked: false},
                {id: 2, name: 'SUV', img: require('@/assets/img/inventory/BodyType/SUV.png'), checked: false},
                {id: 3, name: 'Sedan', img: require('@/assets/img/inventory/BodyType/Sedan.png'), checked: false},
                {id: 4, name: 'Hatchback', img: require('@/assets/img/inventory/BodyType/Hatchback.png'), checked: false},
                {id: 5, name: 'Coupe', img: require('@/assets/img/inventory/BodyType/Coupe.png'), checked: false},
                {id: 6, name: 'Convertiable', img: require('@/assets/img/inventory/BodyType/Convertiable.png'), checked: false},
                {id: 7, name: 'VAN', img: require('@/assets/img/inventory/BodyType/VAN.png'), checked: false}
            ],
            selectTransmission: [
                {id: 1, name: 'Automatic', checked: false},
                {id: 2, name: 'Manual', checked: false}
            ],
            selected: 'Recommendations',
            optionDonesMake: [],
            optionDonesMadel: [],
            optionBodyType: [],
            optionTransmission: [],
            sortedProducts: [],
            filterDisplay: false,
        }
    },

    computed: {
        inventoryCars(){
            return this.$store.getters.inventoryCars
        },
        // searchHandler(){
        //     this.paginatedCars = this.inventoryCars
        //     console.log(this.paginatedCars)
        //     return this.paginatedCars.filter(elem => {
                
        //         return elem.titleCar.toUpperCase().includes(this.search.toUpperCase())
        //     })
        // },
        totalPaginas(){
            if(this.search === ''){
               return Math.ceil(this.filtredProducts.length / this.perPage)  
            } else if(this.res.length <= this.perPage){
                this.pageNumber = 1
                return 1
            } else{
                return Math.ceil(this.res.length / this.perPage)
            } 
        },
        paginatedCars(){
            let from = Math.ceil(this.pageNumber - 1) * this.perPage;
            let to = from + this.perPage;
            window.scrollTo(0,0) 
            //return this.sortedProducts.slice(from, to)
            if(this.search === ''){
                return this.filtredProducts.slice(from, to)
            }else{
                this.res = this.filtredProducts.filter(elem => {
                    return elem.titleCar.toUpperCase().includes(this.search.toUpperCase())
                })
                return this.res.slice(from, to)
            }            
        },
        filterMake(){
            if(this.searchMake === ''){
                return this.selectMake
            } else{
                return this.selectMake.filter(elem => {
                    return elem.name.toUpperCase().includes(this.searchMake.toUpperCase())
                })
            }
            
        },
        filterMadel(){
            if(this.searchModel === ''){
                return this.selectMadel
            } else{
                return this.selectMadel.filter(elem => {
                    return elem.name.toUpperCase().includes(this.searchModel.toUpperCase())
                })
            }
        },
        
        filtredProducts() {
            this.sortedProducts = this.inventoryCars
            return this.sortedProducts

                //Filter Made

                .filter(item => {
                    return this.optionDonesMake.length == 0 || this.optionDonesMake.includes(item.titleCar)
                })

                //Filter Model

                .filter(item => {
                    return this.optionDonesMadel.length == 0 || this.optionDonesMadel.includes(item.titleModel)
                })

                //Filter Type

                .filter(item => {
                    return this.optionBodyType.length == 0 || this.optionBodyType.includes(item.body)
                })

                //Filter Transmission

                .filter(item => {
                    return this.optionTransmission.length == 0 || this.optionTransmission.includes(item.transmission)
                })
                        
                

                //Filter Price
                .filter(item => {
                    return Number(item.price) >= this.minPrice && Number(item.price) <= this.maxPrice
                })

                //Filter Year
                .filter(item => {
                    return Number(item.year) >= this.minYear && Number(item.year) <= this.maxYear
                })

                //Filet Kilometers
                .filter(item => {
                    return Number(item.kilometres) <= this.minKilometres
                })
        },
    },

    methods:{
        
        innerWidth(){
            this.width = window.innerWidth;
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
        },
        pageClick(p){
            this.pageNumber = p;
        },
        cartClick(id){
            this.$router.push({name: 'InventoryCar', query: {'inventory': id}})
        },
        isActive(p){
            return p == this.pageNumber ? 'activeP' : ''
        },
        selectOption(option){
            if(option){
                this.selected = option.name
                if(this.selected === 'Recommendations'){
                    this.sortedProducts.sort((a, b) => a.id - b.id)
                } else if(this.selected === 'Newest inventory'){
                   // this.paginatedCars = []
                    this.sortedProducts.sort((a, b) => b.year - a.year)
                } else if(this.selected === 'Lowest price'){
                    this.sortedProducts.sort((a, b) => a.price - b.price)
                } else if(this.selected === 'Highest price'){
                    this.sortedProducts.sort((a, b) => b.price - a.price)
                } 
            }
            
        },
        clearFilters(){
            this.optionDonesMadel = []
            this.optionDonesMake = []
            this.optionBodyType.forEach(function(item, i, optionBodyType){
                item.checked = false
            })
            this.optionBodyType = []
            this.optionTransmission.forEach(function(item, i, optionTransmission){
                item.checked = false
            })
            this.optionTransmission = []
            if(this.minPrice != 0 || this.maxPrice != 200){
                const parent = document.querySelector('.select-price_done')
                const priceDone = document.querySelector('.option_done-main')
                parent.removeChild(priceDone)
                this.minPrice = 0
                this.maxPrice = 200
                const progress = document.querySelector('.select-option_price .progress');
                progress.style.left = (this.minPrice / 200) * 100 + '%'
                progress.style.right = 100 - (this.maxPrice / 200) * 100 + '%'  
            }
            if(this.minYear != 1980 || this.maxYear != 2023){
                const parentYear = document.querySelector('.select-year_done')
                const yearDone = document.querySelector('.option_done-year')
                parentYear.removeChild(yearDone)
                this.minYear = 1980
                this.maxYear = 2023
                const progress2 = document.querySelector('.select-option_year .progress');
                progress2.style.left = ((this.minYear - 1980) / 43) * 100 + '%'
                progress2.style.right = ((2023 - this.maxYear) / 43) * 100 + '%' 
            }
            if(this.minKilometres != 200){
                const parentKm = document.querySelector('.select-kilometres_done')
                const kmDone = document.querySelector('.option_done-kilometres')
                parentKm.removeChild(kmDone)
                this.minKilometres = 200
                const progress3 = document.querySelector('.select-option_kilometres .progress');
                progress3.style.right = 100 - (this.minKilometres / 200) * 100 + '%'
            }
        },
        selectMadelClick(madel){
            if(!this.optionDonesMadel.includes(madel.name)){
               this.optionDonesMadel.push(madel.name) 
            }
            this.searchModel = ''
        },
        selectMakeClick(make){
            if(!this.optionDonesMake.includes(make.name)){
               this.optionDonesMake.push(make.name) 
            }
            this.searchMake = ''
        },
        inputCheckboxClick(type, index){
            if(!this.optionBodyType.includes(type.name) && type.checked === false){
               return this.optionBodyType.push(type.name)
            } else if(type.checked === true && this.optionBodyType.includes(type.name)){
                //for(let i = 0; i < this.optionBodyType; i++)
                //index = i
                this.optionBodyType.forEach(function(item, i, optionBodyType){
                    if(type.name === item){
                        index = i
                    }
                })
                return this.optionBodyType.splice(index, 1)
            }
        },
        inputTransmClick(type, index){
            if(!this.optionTransmission.includes(type.name) && type.checked === false){
               this.optionTransmission.push(type.name) 
            } else if(type.checked === true && this.optionTransmission.includes(type.name)){
                this.optionTransmission.forEach(function(item, i, optionTransmission){
                        if(type.name === item){
                            index = i
                        }
                })
                return this.optionTransmission.splice(index, 1)
            }
        },
        deleteDoneMake(i){
            this.optionDonesMake.splice(i, 1)
        },
        deleteDoneMadel(i){
            this.optionDonesMadel.splice(i, 1)
        },
        deleteType(optionType, i){
            this.selectBodyType.forEach(item =>{
                if(item.name === optionType){
                    item.checked = false
                }
            })
            this.optionBodyType.splice(i, 1)
        },
        deleteTransm(optionTransm, i){
            this.selectTransmission.forEach(item =>{
                if(item.name === optionTransm){
                    item.checked = false
                }
            })
            this.optionTransmission.splice(i, 1)
            
        },
        rangeSlider(){
            const range = document.querySelectorAll('.select-option_price input');
            const progress = document.querySelector('.select-option_price .progress');
            let gap = 10
            
            progress.style.left = (this.minPrice / 200) * 100 + '%'
            progress.style.right = 100 - (this.maxPrice / 200) * 100 + '%'

            range.forEach(input => {
                input.addEventListener('input', e => {
                    let minRange = this.minPrice
                    let maxRange = this.maxPrice
                    if(maxRange - minRange < gap){
                        if(e.target.className === 'range-min'){
                            this.minPrice = Number(maxRange) - gap
                        } else{
                            this.maxPrice = Number(minRange) + gap
                        }
                    } else{
                        progress.style.left = (minRange / range[0].max) * 100 + '%'
                        progress.style.right = 100 - (maxRange / range[1].max) * 100 + '%'
                    }
                })
            })
        },
        deletePrice(){
            const parent = document.querySelector('.select-price_done')
            const priceDone = document.querySelector('.option_done-main')

            parent.removeChild(priceDone)
            this.minPrice = 0
            this.maxPrice = 200
        },
        rangeSlider2(){
            const range = document.querySelectorAll('.select-option_year input');
            const progress = document.querySelector('.select-option_year .progress');
            let gap = 5
            
            progress.style.left = ((this.minYear - 1980) / 43) * 100 + '%'
            progress.style.right = ((2023 - this.maxYear) / 43) * 100 + '%'

            range.forEach(input => {
                input.addEventListener('input', e => {
                    let minRange = this.minYear - 1980
                    let maxRange = 2023 - this.maxYear
                    if(this.maxYear - this.minYear < gap){
                        if(e.target.className === 'range-min'){
                            this.minYear = Number(this.maxYear) - gap
                        } else{
                            this.maxYear = Number(this.minYear) + gap
                                                    
                        }
                    } else{
                        progress.style.left = (minRange / 43) * 100 + '%'
                        progress.style.right = (maxRange / 43) * 100 + '%'
                    }
                })
            })
        },
        deleteYear(){
            const parentYear = document.querySelector('.select-year_done')
            const yearDone = document.querySelector('.option_done-year')

            parentYear.removeChild(yearDone)
            this.minYear = 1980
            this.maxYear = 2023
        },
        rangeSlider3(){
            const range = document.querySelectorAll('.select-option_kilometres input');
            const progress = document.querySelector('.select-option_kilometres .progress');
            
            progress.style.right = 100 - (this.minKilometres / 200) * 100 + '%'

            range.forEach(input => {
                input.addEventListener('input', e => {
                    let maxRange = this.minKilometres
                    progress.style.right = 100 - (maxRange / 200) * 100 + '%'
                    
                })
            })
        },
        deleteKilometres(){
            const parentKm = document.querySelector('.select-kilometres_done')
            const kmDone = document.querySelector('.option_done-kilometres')

            parentKm.removeChild(kmDone)
            this.minKilometres = 200
        },
        // filterPrice(){
        //     let vm = this
        //     if(this.res.length > 1){
        //         this.res = this.sortedProducts.filter(function(item){
        //             return item.price >= Number(vm.minPrice) && item.price <= Number(vm.maxPrice)
        //         })
        //         return this.sortedProducts = this.res
        //     } else if(this.res.length < 1){
        //         this.res = this.inventoryCars.filter(function(item){
        //             return item.price >= Number(vm.minPrice) && item.price <= Number(vm.maxPrice)
        //         })
        //         return this.sortedProducts = this.res
        //     }
            
        // },
        // filterYear(){
        //     let vm = this
        //     if(this.res.length > 1){
        //         this.res = this.sortedProducts.filter(function(item){
        //             return item.year >= Number(vm.minYear) && item.year <= Number(vm.maxYear)
        //         })
        //         return this.sortedProducts = this.res
        //     } else{
        //         this.res = this.inventoryCars.filter(function(item){
        //             return item.year >= Number(vm.minYear) && item.year <= Number(vm.maxYear)
        //         })
        //         return this.sortedProducts = this.res
        //     }
        // },

    },

    mounted(){
        // this.filterPrice(),
        // this.filterYear(),
        this.filtredProducts
    },

    created() {
        window.addEventListener('resize', this.innerWidth);
        this.innerWidth();
    },

    watch:{
        perPage(){
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
            //this.getDataPaginas(1)
        },
        filterDisplay: {
            immediate: true,
            handler(val) {
           // document.body.classList.toggle('lock', val);
           // document.documentElement.classList.toggle('lock', val);
            },
        },
    },

    components: {
        Swiper,
        SwiperSlide,
        PreloaderVac,
    },
    setup() {
        return {
        modules: [Scrollbar],
        };
    },
    
}
