<style lang="scss">

@import '../assets/scss/breakpoints.scss';

.inventory{
    margin-top: 40px;
}
.inventory-main{
    display: grid;
    grid-template-columns: 30% 70%;
    grid-template-areas: 'filters search' 'filters cars';
    position: relative;
    @include breakpoints(xl){
        grid-template-columns: 20% 80%;
        grid-template-areas: 'search search' 'cars cars';
    }
}

.margintop{
    margin-top: 20px;
}

                                                //Filters

.inventory-main_filters {
    grid-area: filters;
    width: 90%;
    margin-bottom: 40px;
    @include breakpoints(xl){
        display: none;
        position: absolute;
        top: -1px;
        left: -3%;
        right: 0;
        width: 105%;
        background: white;
        z-index: 3;
